"use client";
import React, { useState, useEffect } from "react";
import {
  Mail,
  Lock,
  Shield,
  CheckCircle,
  AlertCircle,
  ArrowRight,
  ArrowLeft,
  Loader2,
  Eye,
  EyeOff,
  Send,
  KeyRound,
  Sparkles,
  Phone,
} from "lucide-react";
import API_BASE_URL from "@/utils/constants";
import { useRouter } from "next/navigation";

const ResetPinPage = () => {
  const [currentStep, setCurrentStep] = useState(1);
  const [identifier, setIdentifier] = useState(""); // email or phone
  const [identifierType, setIdentifierType] = useState("email"); // 'email' or 'phone'
  const [otp, setOtp] = useState(["", "", "", "", "", ""]);
  const [newPin, setNewPin] = useState("");
  const [confirmPin, setConfirmPin] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [resendTimer, setResendTimer] = useState(0);
  const [showNewPin, setShowNewPin] = useState(false);
  const [showConfirmPin, setShowConfirmPin] = useState(false);

  const router = useRouter();

  // Timer for resend OTP
  useEffect(() => {
    if (resendTimer > 0) {
      const timer = setTimeout(() => setResendTimer(resendTimer - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [resendTimer]);

  // Auto-detect identifier type
  useEffect(() => {
    if (identifier.includes("@")) {
      setIdentifierType("email");
    } else if (/^\d+$/.test(identifier) && identifier.length >= 10) {
      setIdentifierType("phone");
    }
  }, [identifier]);

  const validateIdentifier = () => {
    if (!identifier.trim()) {
      setError("Please enter your email or phone number");
      return false;
    }

    if (identifierType === "email") {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(identifier)) {
        setError("Please enter a valid email address");
        return false;
      }
    } else {
      if (identifier.length < 10) {
        setError("Please enter a valid phone number");
        return false;
      }
    }
    return true;
  };

  const sendOTP = async () => {
    if (!validateIdentifier()) return;

    setLoading(true);
    setError("");
    setSuccess("");

    try {
      const response = await fetch(`${API_BASE_URL}/auth/send-otp`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          [identifierType]: identifier,
          type: "PIN_RESET",
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data?.message || "Failed to send OTP");
      }

      setSuccess(`OTP sent successfully to your ${identifierType}`);
      setResendTimer(60);
      setTimeout(() => {
        setCurrentStep(2);
        setSuccess("");
      }, 1500);
    } catch (err) {
      setError(err.message || "Failed to send OTP. Please try again.");
      console.error("Error sending OTP:", err);
    } finally {
      setLoading(false);
    }
  };

  const resendOTP = async () => {
    setOtp(["", "", "", "", "", ""]);
    await sendOTP();
  };

  const handleOtpChange = (index, value) => {
    if (!/^\d*$/.test(value)) return;

    const newOtp = [...otp];
    newOtp[index] = value.slice(-1);
    setOtp(newOtp);
    setError("");

    // Auto-focus next input
    if (value && index < 5) {
      const nextInput = document.getElementById(`otp-${index + 1}`);
      nextInput?.focus();
    }
  };

  const handleOtpKeyDown = (index, e) => {
    if (e.key === "Backspace" && !otp[index] && index > 0) {
      const prevInput = document.getElementById(`otp-${index - 1}`);
      prevInput?.focus();
    }
  };

  const handleOtpPaste = (e) => {
    e.preventDefault();
    const pastedData = e.clipboardData.getData("text").slice(0, 6);
    if (!/^\d+$/.test(pastedData)) return;

    const newOtp = [...otp];
    for (let i = 0; i < pastedData.length; i++) {
      newOtp[i] = pastedData[i];
    }
    setOtp(newOtp);
  };

  const verifyOTP = async () => {
    const otpString = otp.join("");
    if (otpString.length !== 6) {
      setError("Please enter a 6-digit OTP");
      return;
    }

    setLoading(true);
    setError("");

    try {
      const response = await fetch(`${API_BASE_URL}/auth/verify-otp`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          [identifierType]: identifier,
          otp: otpString,
          type: "PIN_RESET",
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data?.message || "Invalid OTP");
      }

      setSuccess("OTP verified successfully!");
      setTimeout(() => {
        setCurrentStep(3);
        setSuccess("");
      }, 1000);
    } catch (err) {
      setError(err.message || "Invalid OTP. Please try again.");
      console.error("Error verifying OTP:", err);
    } finally {
      setLoading(false);
    }
  };

  const resetPin = async () => {
    if (newPin.length !== 6) {
      setError("PIN must be 6 digits");
      return;
    }

    if (newPin !== confirmPin) {
      setError("PINs do not match");
      return;
    }

    setLoading(true);
    setError("");

    try {
      const response = await fetch(`${API_BASE_URL}/auth/reset-pin`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          [identifierType]: identifier,
          pin: newPin,
          timestamp: new Date().toISOString(),
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data?.message || "Failed to reset PIN");
      }

      setCurrentStep(4);
    } catch (err) {
      setError(err.message || "Failed to reset PIN. Please try again.");
      console.error("Error resetting PIN:", err);
    } finally {
      setLoading(false);
    }
  };

  const handlePinChange = (value, type) => {
    if (/^\d*$/.test(value) && value.length <= 6) {
      if (type === "new") {
        setNewPin(value);
      } else {
        setConfirmPin(value);
      }
      setError("");
    }
  };

  const maskIdentifier = (id) => {
    if (!id) return "";
    if (identifierType === "email") {
      const [name, domain] = id.split("@");
      const maskedName = name.substring(0, 2) + "****";
      return `${maskedName}@${domain}`;
    } else {
      return id.substring(0, 3) + "****" + id.substring(id.length - 3);
    }
  };

  // Step 1: Enter Email/Phone
  const renderStep1 = () => (
    <div className="space-y-6">
      {/* Header */}
      <div className="text-center mb-8">
        <div className="relative inline-block">
          <div className="w-20 h-20 mx-auto mb-4 bg-gradient-to-br from-violet-500 to-purple-600 rounded-2xl flex items-center justify-center shadow-lg shadow-purple-500/30 transform rotate-3 hover:rotate-0 transition-transform duration-300">
            <KeyRound className="w-10 h-10 text-white" />
          </div>
          <div className="absolute -top-1 -right-1 w-6 h-6 bg-gradient-to-br from-amber-400 to-orange-500 rounded-full flex items-center justify-center animate-bounce">
            <Sparkles className="w-3 h-3 text-white" />
          </div>
        </div>
        <h3 className="text-2xl font-bold bg-gradient-to-r from-gray-900 via-purple-900 to-violet-900 bg-clip-text text-transparent mb-2">
          Forgot Your PIN?
        </h3>
        <p className="text-gray-500">
          No worries! Enter your email or phone number to reset it
        </p>
      </div>

      {/* Input Field */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">
          Email or Phone Number
        </label>
        <div className="relative group">
          <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
            {identifierType === "email" ? (
              <Mail className="h-5 w-5 text-gray-400 group-focus-within:text-purple-500 transition-colors" />
            ) : (
              <Phone className="h-5 w-5 text-gray-400 group-focus-within:text-purple-500 transition-colors" />
            )}
          </div>
          <input
            type={identifierType === "email" ? "email" : "tel"}
            value={identifier}
            onChange={(e) => {
              setIdentifier(e.target.value);
              setError("");
            }}
            placeholder="Enter your email or phone"
            className="w-full pl-12 pr-4 py-4 border-2 border-gray-200 rounded-xl focus:border-purple-500 focus:ring-4 focus:ring-purple-500/10 focus:outline-none transition-all bg-white/50 backdrop-blur-sm text-gray-900 placeholder-gray-400"
            autoFocus
          />
        </div>
        {identifier && (
          <p className="text-xs text-gray-500 flex items-center gap-1">
            {identifierType === "email" ? (
              <Mail className="w-3 h-3" />
            ) : (
              <Phone className="w-3 h-3" />
            )}
            We&apos;ll send OTP to your {identifierType}
          </p>
        )}
      </div>

      {/* Submit Button */}
      <button
        onClick={sendOTP}
        disabled={loading || !identifier}
        className="w-full bg-gradient-to-r from-violet-600 to-purple-600 text-white py-4 rounded-xl font-semibold hover:from-violet-700 hover:to-purple-700 transform hover:-translate-y-0.5 hover:shadow-lg hover:shadow-purple-500/25 transition-all disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none disabled:shadow-none flex items-center justify-center group"
      >
        {loading ? (
          <>
            <Loader2 className="w-5 h-5 animate-spin mr-2" />
            Sending OTP...
          </>
        ) : (
          <>
            Send Reset Code
            <Send className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
          </>
        )}
      </button>

      {/* Back to Login */}
      <div className="text-center">
        <button
          onClick={() => router.push("/login")}
          className="text-sm text-gray-500 hover:text-purple-600 font-medium transition-colors inline-flex items-center gap-1"
        >
          <ArrowLeft className="w-4 h-4" />
          Back to Login
        </button>
      </div>
    </div>
  );

  // Step 2: Verify OTP
  const renderStep2 = () => (
    <div className="space-y-6">
      {/* Header */}
      <div className="text-center mb-6">
        <div className="w-20 h-20 mx-auto mb-4 bg-gradient-to-br from-emerald-500 to-teal-600 rounded-2xl flex items-center justify-center shadow-lg shadow-emerald-500/30">
          <Mail className="w-10 h-10 text-white" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 mb-2">
          Verify Your Identity
        </h3>
        <p className="text-gray-500">
          We sent a 6-digit code to{" "}
          <span className="font-semibold text-gray-700">
            {maskIdentifier(identifier)}
          </span>
        </p>
      </div>

      {/* OTP Input */}
      <div className="flex justify-center gap-2 sm:gap-3">
        {otp.map((digit, index) => (
          <input
            key={index}
            id={`otp-${index}`}
            type="text"
            inputMode="numeric"
            maxLength="1"
            value={digit}
            onChange={(e) => handleOtpChange(index, e.target.value)}
            onKeyDown={(e) => handleOtpKeyDown(index, e)}
            onPaste={handleOtpPaste}
            className="w-11 h-14 sm:w-12 sm:h-16 text-center text-xl sm:text-2xl font-bold border-2 border-gray-200 rounded-xl focus:border-emerald-500 focus:ring-4 focus:ring-emerald-500/10 focus:outline-none transition-all bg-white text-gray-900"
            autoFocus={index === 0}
          />
        ))}
      </div>

      {/* Resend Timer */}
      <div className="text-center">
        {resendTimer > 0 ? (
          <p className="text-gray-500 text-sm">
            Resend code in{" "}
            <span className="font-semibold text-emerald-600 tabular-nums">
              {resendTimer}s
            </span>
          </p>
        ) : (
          <button
            onClick={resendOTP}
            disabled={loading}
            className="text-emerald-600 hover:text-emerald-700 font-medium text-sm underline underline-offset-4 disabled:opacity-50 transition-colors"
          >
            Didn&apos;t receive code? Resend
          </button>
        )}
      </div>

      {/* Verify Button */}
      <button
        onClick={verifyOTP}
        disabled={loading || otp.join("").length !== 6}
        className="w-full bg-gradient-to-r from-emerald-600 to-teal-600 text-white py-4 rounded-xl font-semibold hover:from-emerald-700 hover:to-teal-700 transform hover:-translate-y-0.5 hover:shadow-lg hover:shadow-emerald-500/25 transition-all disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center"
      >
        {loading ? (
          <Loader2 className="w-5 h-5 animate-spin" />
        ) : (
          <>
            Verify Code
            <ArrowRight className="w-5 h-5 ml-2" />
          </>
        )}
      </button>

      {/* Back Button */}
      <button
        onClick={() => {
          setCurrentStep(1);
          setOtp(["", "", "", "", "", ""]);
          setResendTimer(0);
          setError("");
        }}
        className="w-full text-gray-500 hover:text-gray-700 font-medium transition-colors flex items-center justify-center gap-2"
      >
        <ArrowLeft className="w-4 h-4" />
        Change {identifierType}
      </button>
    </div>
  );

  // Step 3: Set New PIN
  const renderStep3 = () => (
    <div className="space-y-6">
      {/* Header */}
      <div className="text-center mb-6">
        <div className="w-20 h-20 mx-auto mb-4 bg-gradient-to-br from-blue-500 to-indigo-600 rounded-2xl flex items-center justify-center shadow-lg shadow-blue-500/30">
          <Lock className="w-10 h-10 text-white" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 mb-2">
          Create New PIN
        </h3>
        <p className="text-gray-500">
          Choose a secure 6-digit PIN for your account
        </p>
      </div>

      {/* New PIN Input */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">
          New PIN
        </label>
        <div className="relative group">
          <input
            type={showNewPin ? "text" : "password"}
            maxLength="6"
            value={newPin}
            onChange={(e) => handlePinChange(e.target.value, "new")}
            placeholder="••••••"
            className="w-full px-4 py-4 text-center text-2xl font-bold tracking-[0.5em] border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10 focus:outline-none transition-all bg-white text-gray-900 placeholder-gray-300"
            autoComplete="off"
            autoFocus
          />
          <button
            type="button"
            onClick={() => setShowNewPin(!showNewPin)}
            className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
          >
            {showNewPin ? (
              <EyeOff className="w-5 h-5" />
            ) : (
              <Eye className="w-5 h-5" />
            )}
          </button>
        </div>
        {/* PIN Strength Indicator */}
        {newPin.length > 0 && (
          <div className="flex gap-1 mt-2">
            {[...Array(6)].map((_, i) => (
              <div
                key={i}
                className={`h-1 flex-1 rounded-full transition-all ${i < newPin.length
                    ? newPin.length < 4
                      ? "bg-red-400"
                      : newPin.length < 6
                        ? "bg-amber-400"
                        : "bg-emerald-400"
                    : "bg-gray-200"
                  }`}
              />
            ))}
          </div>
        )}
      </div>

      {/* Confirm PIN Input */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">
          Confirm PIN
        </label>
        <div className="relative group">
          <input
            type={showConfirmPin ? "text" : "password"}
            maxLength="6"
            value={confirmPin}
            onChange={(e) => handlePinChange(e.target.value, "confirm")}
            placeholder="••••••"
            className="w-full px-4 py-4 text-center text-2xl font-bold tracking-[0.5em] border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10 focus:outline-none transition-all bg-white text-gray-900 placeholder-gray-300"
            autoComplete="off"
          />
          <button
            type="button"
            onClick={() => setShowConfirmPin(!showConfirmPin)}
            className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
          >
            {showConfirmPin ? (
              <EyeOff className="w-5 h-5" />
            ) : (
              <Eye className="w-5 h-5" />
            )}
          </button>
        </div>
      </div>

      {/* PIN Match Indicator */}
      {newPin.length === 6 && confirmPin.length > 0 && (
        <div
          className={`p-3 rounded-xl flex items-center gap-2 ${newPin === confirmPin
              ? "bg-emerald-50 text-emerald-700 border border-emerald-200"
              : "bg-amber-50 text-amber-700 border border-amber-200"
            }`}
        >
          {newPin === confirmPin ? (
            <>
              <CheckCircle className="w-4 h-4 flex-shrink-0" />
              <span className="text-sm font-medium">PINs match!</span>
            </>
          ) : (
            <>
              <AlertCircle className="w-4 h-4 flex-shrink-0" />
              <span className="text-sm font-medium">PINs don&apos;t match</span>
            </>
          )}
        </div>
      )}

      {/* Reset Button */}
      <button
        onClick={resetPin}
        disabled={
          loading ||
          newPin.length !== 6 ||
          confirmPin.length !== 6 ||
          newPin !== confirmPin
        }
        className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 text-white py-4 rounded-xl font-semibold hover:from-blue-700 hover:to-indigo-700 transform hover:-translate-y-0.5 hover:shadow-lg hover:shadow-blue-500/25 transition-all disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center"
      >
        {loading ? (
          <>
            <Loader2 className="w-5 h-5 animate-spin mr-2" />
            Resetting PIN...
          </>
        ) : (
          <>
            Reset PIN
            <Shield className="w-5 h-5 ml-2" />
          </>
        )}
      </button>
    </div>
  );

  // Step 4: Success
  const renderStep4 = () => (
    <div className="text-center py-6">
      {/* Success Animation */}
      <div className="relative inline-block mb-6">
        <div className="w-24 h-24 mx-auto bg-gradient-to-br from-emerald-400 to-green-500 rounded-full flex items-center justify-center shadow-xl shadow-emerald-500/40 animate-bounce">
          <CheckCircle className="w-12 h-12 text-white" />
        </div>
        <div className="absolute -inset-4 bg-gradient-to-r from-emerald-400/20 to-green-400/20 rounded-full blur-xl animate-pulse" />
      </div>

      <h3 className="text-3xl font-bold bg-gradient-to-r from-emerald-600 to-green-600 bg-clip-text text-transparent mb-3">
        PIN Reset Successful!
      </h3>
      <p className="text-gray-500 mb-2">
        Your PIN has been successfully updated
      </p>
      <p className="text-sm text-gray-400 mb-8">
        You can now use your new PIN for transactions
      </p>

      {/* Action Buttons */}
      <div className="space-y-3">
        <button
          onClick={() => router.push("/login")}
          className="w-full bg-gradient-to-r from-emerald-600 to-green-600 text-white py-4 rounded-xl font-semibold hover:from-emerald-700 hover:to-green-700 transform hover:-translate-y-0.5 hover:shadow-lg hover:shadow-emerald-500/25 transition-all flex items-center justify-center group"
        >
          Continue to Login
          <ArrowRight className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
        </button>
      </div>
    </div>
  );

  // Progress Steps Indicator
  const renderProgressSteps = () => (
    <div className="flex items-center justify-center gap-2 mb-8">
      {[1, 2, 3].map((step) => (
        <React.Fragment key={step}>
          <div
            className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-semibold transition-all duration-300 ${currentStep > step
                ? "bg-gradient-to-r from-emerald-500 to-green-500 text-white shadow-lg shadow-emerald-500/30"
                : currentStep === step
                  ? "bg-gradient-to-r from-violet-500 to-purple-500 text-white shadow-lg shadow-purple-500/30 scale-110"
                  : "bg-gray-100 text-gray-400"
              }`}
          >
            {currentStep > step ? (
              <CheckCircle className="w-4 h-4" />
            ) : (
              step
            )}
          </div>
          {step < 3 && (
            <div
              className={`w-12 h-1 rounded-full transition-all duration-500 ${currentStep > step
                  ? "bg-gradient-to-r from-emerald-500 to-green-500"
                  : "bg-gray-200"
                }`}
            />
          )}
        </React.Fragment>
      ))}
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-purple-50 to-blue-50 flex items-center justify-center p-4 sm:p-6 md:p-8 relative overflow-hidden">
      {/* Animated Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-1/4 -left-20 w-72 h-72 bg-purple-300/30 rounded-full blur-3xl animate-pulse" />
        <div className="absolute bottom-1/4 -right-20 w-80 h-80 bg-blue-300/30 rounded-full blur-3xl animate-pulse delay-700" />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-violet-300/20 rounded-full blur-3xl animate-pulse delay-1000" />
      </div>

      {/* Main Card */}
      <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl max-w-md w-full p-6 sm:p-8 border border-white/50 relative z-10">
        {/* Step Content */}
        <div className="relative">
          {/* Progress Steps - Hide on success */}
          {currentStep < 4 && renderProgressSteps()}

          {/* Error Message */}
          {error && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-xl flex items-start gap-3 text-red-700 animate-shake">
              <AlertCircle className="w-5 h-5 flex-shrink-0 mt-0.5" />
              <span className="text-sm font-medium">{error}</span>
            </div>
          )}

          {/* Success Message */}
          {success && (
            <div className="mb-6 p-4 bg-emerald-50 border border-emerald-200 rounded-xl flex items-start gap-3 text-emerald-700">
              <CheckCircle className="w-5 h-5 flex-shrink-0 mt-0.5" />
              <span className="text-sm font-medium">{success}</span>
            </div>
          )}

          {/* Step Content with Animation */}
          <div className="transition-all duration-300 ease-in-out">
            {currentStep === 1 && renderStep1()}
            {currentStep === 2 && renderStep2()}
            {currentStep === 3 && renderStep3()}
            {currentStep === 4 && renderStep4()}
          </div>
        </div>
      </div>

      {/* Add custom animation keyframes */}
      <style jsx>{`
        @keyframes shake {
          0%, 100% { transform: translateX(0); }
          10%, 30%, 50%, 70%, 90% { transform: translateX(-4px); }
          20%, 40%, 60%, 80% { transform: translateX(4px); }
        }
        .animate-shake {
          animation: shake 0.6s ease-in-out;
        }
      `}</style>
    </div>
  );
};

export default ResetPinPage;
